<template>
  <div>
    <da-manage-page v-show="!formVisible">
      <div slot="button">
        <el-button icon="el-icon-my-zengjia" @click="createForm">创建表单</el-button>
        <!-- <el-button type="primary" @click="openFullScreen" v-loading.lock="fullscreenLoading">
          指令方式
        </el-button> -->
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="表名">
          <el-input v-model="searchForm.formdata.tableName"></el-input>
        </el-form-item>
        <el-form-item label="说明">
          <el-input v-model="searchForm.formdata.notes"></el-input>
        </el-form-item>
        <el-form-item label="类名">
          <el-input v-model="searchForm.formdata.className"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="mctable" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="true">
        <el-table-column property="operation" label="操作" width="80" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editForm(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </da-table>
    </da-manage-page>
    <form-page :visible.sync="formVisible"></form-page>
    <!-- <form-template></form-template> -->
  </div>
</template>
<script>
import formPage from "./form";
import formTemplate from "./inputTemplate";
export default {
  name: "moduleConfiguration",
  components: {
    formPage,
    formTemplate
  },
  data() {
    return {
      // 查询表单
      searchForm: {
        formdata: {
          tableName: "",
          notes: "",
          className: ""
        },
        currentPage: 1,
        pageSize: 10
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        {
          label: "表名",
          prop: "tableName",
          align: "left",
          sortable: true
        },
        {
          label: "说明",
          prop: "notes",
          align: "left",
          sortable: true
        },
        {
          label: "类名",
          prop: "className",
          sortable: true
        },
        {
          label: "模块",
          prop: "moduleName",
          align: "left",
          sortable: true
        },
        {
          label: "时间",
          prop: "dateTime",
          align: "left",
          sortable: true
        }
      ],
      formVisible: false
    };
  },
  methods: {
    getTbList() {
      this.$ajax
        .get("rest/autoparam/page", {
          params: this.searchForm,
          loadingTarget: this.$refs.mctable
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    createForm() {
      this.formVisible = true;
    },
    editForm(row) {
      this.formVisible = true;
      //   this.inputForm = JSON.parse(JSON.stringify(row));
    },
    openFullScreen() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
      }, 2000);
    }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



