<template>
  <div v-show="visible">
    <el-steps :active="stepActive" finish-status="success">
      <el-step title="步骤 1">
      </el-step>
      <el-step title="步骤 2">
      </el-step>
      <el-step title="步骤 3"></el-step>
    </el-steps>
    <el-form ref="databaseForm" :model="databaseForm" :rules="databaseRule" label-width="80px" v-show="stepActive === 0">
      <el-form-item label="表名" prop="tableName">
        <el-select v-model="databaseForm.tableName" filterable size="medium" placeholder="请选择" @change="getForeign">
          <el-option v-for="(item, index) in databaseLists" :key="index" :label="item.tableName+'('+item.notes+')'" :value="item.tableName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="外键">
        <el-select v-model="mainTableInfoIndex" multiple placeholder="请选择" @change="getSelectObj">
          <el-option v-for="(item, index) in foreignKeyLists" :key="index" :label="item.parentTableName" :value="index">
            <!-- item.childField+'.'+item.parentTableName+'.'+item.parentField -->
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button @click="stepOne">下一步</el-button>
      </el-form-item>
    </el-form>

    <el-form label-width="120px" :rules="detailForm" v-show="stepActive ===1">
      <el-form-item label="文件夹名称" prop="moduleName">
        <!-- <el-input v-model="moduleName" type="text"></el-input> -->
        <el-select v-model="moduleName" allow-create filterable placeholder="请选择">
          <el-option v-for="item in folderLists" :key="item.value" :label="item.name" :value="item.value"></el-option>
        </el-select>
        <small>可选择也可填写新增名称，且只能填写英文单词
          <!-- ,或选择已有文件夹名称：<a class="folder-item" v-for="(item, index) in folderLists" :key="index" @click="moduleName=item.value">{{item.value}}</a> -->
        </small>
      </el-form-item>
      <el-form-item label="模块名称">
        <el-input v-model="notes" type="text"></el-input>
      </el-form-item>
      <el-form-item label="类名">
        <el-input v-model="className" type="text"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="stepActive--;">上一步</el-button>
        <el-button @click="getField">下一步</el-button>
      </el-form-item>
    </el-form>

    <div v-show="fieldVisible&&stepActive === 2">
      <da-table :colums="colsData" :table-list="tableList" :showSelection="false">

        <el-table-column label="字段属性" align="center">
          <el-table-column label="列名" width="80" align="center">
            <template slot-scope="scope">{{tableForm[scope.$index].fieldName}}</template>
          </el-table-column>
          <el-table-column label="表头/表单名称" min-width="160" align="center">
            <template slot-scope="scope">
              <el-input v-model="tableForm[scope.$index].fieldNote" type="text"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="字段类型" width="80" align="center">
            <template slot-scope="scope">
              {{tableForm[scope.$index].fieldType}}
            </template>
          </el-table-column>
          <el-table-column label="必填" width="80" align="center">
            <template slot-scope="scope">
              <el-checkbox v-model="tableForm[scope.$index].isRequired" :true-label="1" :false-label="0"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column label="新增" width="80" align="center">
            <template slot-scope="scope">
              <el-checkbox v-model="tableForm[scope.$index].isAdd" :true-label="1" :false-label="0"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column label="编辑" width="80" align="center">
            <template slot-scope="scope">
              <el-checkbox v-model="tableForm[scope.$index].isUpdate" :true-label="1" :false-label="0"></el-checkbox>
            </template>
          </el-table-column>
        </el-table-column>

        <el-table-column label="列表属性" align="center">
          <el-table-column label="是否返回到前台" width="80" align="center">
            <template slot-scope="scope">
              <el-checkbox v-model="tableForm[scope.$index].isDataList" :true-label="1" :false-label="0"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column label="是否不显示在表格上" width="80" align="center">
            <template slot-scope="scope">
              <el-checkbox v-model="tableForm[scope.$index].isHidden" :true-label="1" :false-label="0"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column label="查询字段" width="80" align="center">
            <template slot-scope="scope">
              <el-checkbox v-model="tableForm[scope.$index].isSearch" :true-label="1" :false-label="0"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column label="匹配方式" min-width="160" align="center">
            <!---->
            <template slot-scope="scope">
              <el-select v-model="tableForm[scope.$index].matchModeId" placeholder="请选择">
                <el-option v-for="item in matchModeLists" :key="item.id" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </template>
          </el-table-column>
        </el-table-column>

        <el-table-column label="表单属性" align="center">
          <el-table-column label="表单类型" min-width="160" align="center">
            <template slot-scope="scope">
              <!-- <el-input v-model="tableForm[scope.$index].formModeId" type="text"></el-input> -->
              <el-select v-model="tableForm[scope.$index].formModeId" placeholder="请选择">
                <el-option v-for="item in formTypeLists" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="字典类型" min-width="160" align="center">
            <template slot-scope="scope">
              <!-- <el-input v-model="tableForm[scope.$index].dictionaryType" type="text"></el-input> -->
              <el-select v-model="tableForm[scope.$index].dictionaryType" placeholder="请选择">
                <el-option v-for="item in dictionaryLists" :key="item.code" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </template>
          </el-table-column>
        </el-table-column>
        <!-- <el-table-column property="operation" label="操作" width="80" slot="operation" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
                </template>
            </el-table-column> -->
      </da-table>
      <div class="footer-btn">
        <el-button @click="stepActive--;">上一步</el-button>
        <el-button @click="saveFieldForm">生成页面</el-button>
        <el-button @click="closeForm">返回</el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      stepActive: 0,
      fieldVisible: false,
      formTypeLists: [],
      databaseLists: [],
      matchModeLists: [
        { label: "小于", value: "LESS" },
        { label: "小于等于", value: "LESSEQUAL" },
        { label: "大于", value: "GREATER" },
        { label: "大于等于", value: "GREATEREQUAL" },
        { label: "BETWEEN", value: "BETWEEN" },
        { label: "LIKE", value: "LIKE" },
        { label: "LEFTLIKE", value: "LEFTLIKE" },
        { label: "RIGHTLIKE", value: "RIGHTLIKE" },
        { label: "不等于", value: "NOTEQUALS" },
        { label: "等于", value: "NULL" }
      ],
      foreignKeyLists: [],
      databaseForm: {
        schemaName: "ysems",
        tableName: "",
        mainTableInfo: []
      },
      databaseRule: this.eachValidate({
        tableName: { required: { required: true, trigger: "change" } }
      }),
      detailForm: this.eachValidate({
        moduleName: { required: true, letter: true }
      }),
      mainTableInfoIndex: [],
      moduleName: "",
      notes: "",
      className: "",
      tableList: { rows: [] },
      colsData: [],
      tableForm: [],
      folderLists: [],
      dictionaryLists: []
    };
  },
  methods: {
    stepOne() {
      this.$refs.databaseForm.validate(valid => {
        if (valid) {
          this.stepActive++;
          this.getFolder();
        }
      });
    },
    closeForm() {
      this.$emit("update:visible", false);
    },
    getFormType() {
      this.$ajax.get("rest/autoparam/formType").then(res => {
        this.formTypeLists = res.data.records;
      });
    },
    getFolder() {
      this.$ajax.get("rest/autoparam/fileFolder").then(res => {
        this.folderLists = res.data.records;
      });
    },
    getDictionary() {
      this.$ajax.get("rest/autoparam/dictionary").then(res => {
        this.dictionaryLists = res.data.records;
      });
    },
    // 获取数据库表名
    getDatabase() {
      this.$ajax
        .get("rest/autoparam/tables", {
          params: { schemaName: this.databaseForm.schemaName }
        })
        .then(res => {
          this.databaseLists = res.data.records;
        });
    },
    getForeign() {
      this.$ajax
        .get("rest/autoparam/tableInfo", {
          params: {
            schemaName: this.databaseForm.schemaName,
            tableName: this.databaseForm.tableName
          }
        })
        .then(res => {
          if (res.data.status) {
            this.foreignKeyLists = res.data.records.foreignInfos || [];
            // var humpName = this.databaseForm.tableName.replace(
            //   /_(\w)/g,
            //   function(all, letter) {
            //     return letter.toUpperCase();
            //   }
            // );
            this.mainTableInfoIndex = [];
            this.moduleName = res.data.records.moduleName;
            this.className = res.data.records.className;
            this.notes = res.data.records.notes;
          }
        });
    },
    getField() {
      this.stepActive = 2;
      this.getDictionary();
      this.$ajax
        .get("rest/autoparam/columns", {
          params: this.databaseForm
        })
        .then(res => {
          this.tableList = { records: res.data.records };
          if (res.data.records.length > 0) {
            this.tableForm = res.data.records.map(item => {
              return {
                //字段属性
                tableName: item.tableName, //表名
                fieldName: item.fieldName.split(".")[1], //列名
                fieldNote: item.fieldNote, //默认为列注释，可修改为列表表头或新增属性名
                fieldLen: item.fieldLen || "", //字段长度
                fieldType: item.fieldType, //字段类型，MYSQL中地类型，如varchar
                isRequired: item.isRequired === "YES" ? 1 : 0, //是否必填
                isHidden: item.isHidden, //是否隐藏
                isAdd: item.isAdd, //是否新增
                isUpdate: item.isUpdate, //是否编辑

                //列表属性
                isDataList: item.isDataList, //是否在列表中显示
                isSearch: item.isSearch, //是否为查询字段
                matchModeId: item.matchModeId, //匹配方式
                //新增编辑表单属性
                formModeId: item.formModeId, //表单类型
                dictionaryType: item.dictionaryType //字典类型
              };
            });
          }
          this.fieldVisible = true;
        });
    },
    getSelectObj() {
      if (this.mainTableInfoIndex.length > 0) {
        this.databaseForm.mainTableInfo = this.mainTableInfoIndex
          .map(index => {
            var item = this.foreignKeyLists[index];
            return (
              item.childField +
              "." +
              item.parentTableName +
              "." +
              item.parentField
            );
          })
          .join(",");
      } else {
        this.databaseForm.mainTableInfo = "";
      }
    },
    saveFieldForm() {
      var tableInfo = {
        tableName: this.databaseForm.tableName,
        notes: this.notes,
        className: this.className,
        moduleName: this.moduleName,
        foreignInfos:
          this.mainTableInfoIndex.length > 0
            ? this.mainTableInfoIndex.map(index => {
                return this.foreignKeyLists[index];
              })
            : []
      };
      this.$ajax
        .post("rest/autoparam", {
          tableInfo,
          fieldInfos: this.tableForm
        })
        .then(res => {
          console.log(res);
        });
    }
  },
  mounted() {
    this.getFormType();
    this.getDatabase();
  },
  activated() {}
};
</script>

<style scoped>
.footer-btn {
  text-align: right;
}
.folder-item {
  padding: 0 5px;
  cursor: pointer;
}
.folder-item:hover {
  color: lightslategray;
  text-decoration: underline;
}
</style>



