<template>
    <div class="tp_wrap">
        <el-form ref="form" :model="form" label-width="170px">
            <el-form-item label="输入框">
                <el-input v-model="form.innput"></el-input>
            </el-form-item>
            <el-form-item label="多行输入框">
                <el-input v-model="form.innput" :row="6" type="textarea"></el-input>
            </el-form-item>
            <el-form-item label="下拉单选列表">
                <el-select v-model="form.select" filterable placeholder="请选择">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="下拉多选列表">
                <el-select v-model="form.multipleSelect" filterable multiple placeholder="请选择">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="下拉树形列表">
                <da-select-tree v-model="form.selectTree" :data="industryTree" :props="{label:'label',children:'children'}">
                </da-select-tree>
            </el-form-item>
            <el-form-item label="下拉多选树形列表">
                <da-select-tree v-model="form.selectTree" :multiple="true" :data="industryTree" :props="{label:'label',children:'children'}">
                </da-select-tree>
            </el-form-item>
            <el-form-item label="raido">
                <el-radio-group v-model="form.radio">
                    <el-radio v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="checkbox">
                <el-checkbox-group v-model="form.checkbox">
                    <el-checkbox v-for="item in options" :key="item.value" :label="item.value">{{item.label}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="开关">
                <el-switch v-model="form.switch" active-color="#13ce66" inactive-color="#ff4949" active-value="1" inactive-value="0">
                </el-switch>
            </el-form-item>
            <el-form-item label="滑块">
                <el-slider v-model="form.slider"></el-slider>
            </el-form-item>
            <el-form-item label="时间选择框（年）">
                <el-date-picker v-model="form.year" type="year" placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="时间选择框（月）">
                <el-date-picker v-model="form.month" type="month" placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="时间选择框（周）">
                <el-date-picker v-model="form.week" type="week" format="yyyy 第 WW 周" placeholder="选择周">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="时间选择框（日）">
                <el-date-picker v-model="form.date" type="date" placeholder="选择日期">
                </el-date-picker>
            </el-form-item>

            <el-form-item label="时间选择框（年-秒）">
                <el-date-picker v-model="form.datetime" type="datetime" placeholder="选择日期">
                </el-date-picker>
            </el-form-item>

            <el-form-item label="时间范围选择框（日）">
                <da-time v-model="form.daterange" type="daterange">
                </da-time>
            </el-form-item>
            <el-form-item label="时间范围选择框（月）">
                <da-time v-model="form.monthrange" type="month">
                </da-time>
            </el-form-item>
            <el-form-item label="时间范围选择框（年）">
                <da-time v-model="form.monthrange" type="year">
                </da-time>
            </el-form-item>
            <el-form-item label="时间范围选择框（年-秒）">
                <da-time v-model="form.datetimerange" type="datetimerange">
                </da-time>
            </el-form-item>
            <el-form-item label="文件上传">
                <da-upload v-model="form.upload" url="XXXX"></da-upload>
            </el-form-item>
            <el-form-item>
                <el-button @click="getData">确定</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
  data() {
    return {
      options: [
        { label: "选项一", value: 1 },
        { label: "选项二", value: 2 },
        { label: "选项三", value: 3 },
        { label: "选项四", value: 4 }
      ],
      industryTree: [
        {
          id: 1,
          label: "一级 1",
          children: [
            {
              id: 4,
              label: "二级 1-1",
              children: [
                {
                  id: 9,
                  label: "三级 1-1-1"
                },
                {
                  id: 10,
                  label: "三级 1-1-2"
                }
              ]
            }
          ]
        },
        {
          id: 2,
          label: "一级 2",
          children: [
            {
              id: 5,
              label: "二级 2-1"
            },
            {
              id: 6,
              label: "二级 2-2"
            }
          ]
        },
        {
          id: 3,
          label: "一级 3",
          children: [
            {
              id: 7,
              label: "二级 3-1"
            },
            {
              id: 8,
              label: "二级 3-2"
            }
          ]
        }
      ],
      form: {
        innput: "",
        select: "",
        multipleSelect: "",
        selectTree: "",
        radio: "",
        checkbox: [],
        switch: "",
        slider: 0,
        year: "",
        date: "",
        month: "",
        week: "",
        datetime: "",
        daterange: [],
        datetimerange: [],
        monthrange: [],
        upload: ""
      }
    };
  },
  methods: {
    getData() {
      console.log(this.form);
    }
  }
};
</script>

<style scoped>
.tp_wrap {
  width: 600px;
  margin: 0 auto;
}
</style>



